import {AfterViewInit, Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {DataService} from '../services/dataService/data.service';
import {CookieService} from 'angular2-cookie/core';
import {NgForage, NgForageCache} from 'ngforage';
import {Router} from '@angular/router';

@Component({
  selector: 'app-check-requirements',
  templateUrl: './check-requirements.component.html',
  styleUrls: ['./check-requirements.component.scss']
})
export class CheckRequirementsComponent implements AfterViewInit, OnDestroy {

  constructor(private dataService: DataService,
              private router: Router,
              private cookieService: CookieService) { }

  @ViewChild('loginModal') public showModalOnClick: any;

  public showModal(): void {
    this.showModalOnClick.show();
  }

  public hideModal(): void {
    this.showModalOnClick.hide();
  }

  ngAfterViewInit() {
    this.showModal();
    console.log(navigator.onLine);
    navigator.geolocation.watchPosition(function(position) {
        console.log('i\'m tracking you!');
      },
      function (error) {
        if (error.code === error.PERMISSION_DENIED) {
          console.log('you denied me :-(');
        }
      });
  }

  ngOnDestroy() {
    this.hideModal();
  }


}



