import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {Entity} from 'breeze-client';
import {DataManagerService} from '../../services/data-manager.service';

@Component({
  selector: 'app-step03c-particolare-danno',
  templateUrl: './step03c-particolare-danno.component.html',
  styleUrls: ['./step03c-particolare-danno.component.scss']
})
export class Step03cParticolareDannoComponent implements OnInit {
  @Input() dossierBene: Entity;

  livelli = [];
  selectedID;
  selectedLivello = '';


  constructor(private dataManager: DataManagerService) {
  }

  ngOnInit() {
    this.dataManager.getContext().then(context => {
      this.livelli = context.getEntities('LivelloDanno');
      this.selectedID = this.dossierBene['IDLivelloDanno'];
    });
  }

  setLivello(livello) {
    this.selectedID = livello['ID'];
    this.selectedLivello = livello['Descrizione'];
  }

  confirm() {
    if (!this.selectedID) {
      return;
    }

    this.dataManager.getContext().then(context => {
      this.dossierBene['IDLivelloDanno'] = this.selectedID;
      this.dataManager.saveContext(context);
    });
  }

}



