import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {Entity} from 'breeze-client';
import {DataManagerService} from '../services/data-manager.service';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.scss']
})
export class GalleryComponent implements OnInit {

  allegati: Entity[] = null;
  allEntityAllegati: Entity[] = null;
  @Output() loaded = new EventEmitter<any>();
  @Input() size: number;
  photoHidden = true;
  videoHidden = true;
  previewHidden = true;
  selectedImage: Entity = null;
  // if null DossierNominativo will be used
  @Input() entita: Entity;
  // Used to compare attachment attached the same entity but used in different steps such as causa and fatture
  @Input() type: string = null;
  // Initialized in Init based on entity
  entityProperties = {
    tipoEntita: '',
    percorso: null,
    codiceEntita: null,
    idEntita: null
  };

  constructor(private sanitizer: DomSanitizer, private dataManager: DataManagerService) {
  }

  ngOnInit() {
    if (!this.entita) {
      this.entityProperties.tipoEntita = 'DOSSIERNOMINATIVO';
    } else {
      this.entityProperties.tipoEntita = this.entita.entityType.shortName;
    }

    this.dataManager.getContext().then(context => {
      const nominativo = context.getEntities('DossierNominativoView')[0];

      if (!this.entita) {
        this.entita = nominativo;
      }

      if (!this.entita['custom_Allegati']) {
        this.entita['custom_Allegati'] = {};
        this.entita['custom_Allegati']['custom_all'] = [];
      }
      if (!this.entita['custom_Allegati'][this.type]) {
        this.entita['custom_Allegati'][this.type] = [];
      }
      this.allegati = this.entita['custom_Allegati'][this.type];
      this.allEntityAllegati = this.entita['custom_Allegati']['custom_all'];

      const basePath = 'UploadFile/' + 'DOSSIER/' + nominativo['Riferimento'] + '/DOSSIERNOMINATIVO/' + nominativo['Nominativo'];
      switch (this.entityProperties.tipoEntita) {
        case 'DossierBene_Ext':
          this.entityProperties.tipoEntita = 'DOSSIERBENE_EXT';
          const perizia = this.entita['DossierGaranzia_Ext']['DossierPerizia_Ext'];
          const garanzia = this.entita['DossierGaranzia_Ext'];

          this.entityProperties.codiceEntita = () => this.entita['Descrizione'];
          this.entityProperties.percorso = () => {
            return basePath + '/DOSSIERPERIZIA_EXT/' + perizia['ID'] +
              '/DOSSIERGARANZIA_EXT/' + garanzia['ID'] +
              '/DOSSIERBENE_EXT/' + this.entita['Descrizione'];
          };
          this.entityProperties.idEntita = () => this.entita['ID'];
          break;
        default:
          this.entityProperties.codiceEntita = () => nominativo['Nominativo'];
          this.entityProperties.percorso = () => basePath;
          this.entityProperties.idEntita = () => nominativo['IDDossierNominativo'];
          break;
      }
      this.loaded.emit();
    });
  }

  addImage(image: Blob) {
    this.photoHidden = true;
    this.saveObj(image, '.png');
  }

  addVideo(video: any) {
    this.saveObj(video.video, video.type);
  }

  openImage(image) {
    this.previewHidden = false;
    this.selectedImage = image;
  }

  removePhoto() {
    this.dataManager.getContext().then(context => {
      context.detachEntity(this.selectedImage);
      this.allegati.splice(this.allegati.indexOf(this.selectedImage), 1);
      this.allEntityAllegati.splice(this.allEntityAllegati.indexOf(this.selectedImage), 1);

      this.allEntityAllegati
        .sort((a, b) => a['Progressivo'] - b['Progressivo'])
        .forEach((allegato, index) => {
          allegato['Progressivo'] = index;
        });

      this.dataManager.saveContext(context);

      this.previewHidden = true;
      this.selectedImage = null;
    });
  }

  reset() {
    this.allegati = [];
    this.photoHidden = true;
    this.previewHidden = true;
    this.selectedImage = null;
  }

  saveObj(obj: Blob, type: string) {
    this.dataManager.getContext().then(context => {

        const nuovo_allegato = this.dataManager.addAllegato(context);
        nuovo_allegato['TipoEntita'] = this.entityProperties.tipoEntita;
        nuovo_allegato['IDEntita'] = this.entityProperties.idEntita();
        nuovo_allegato['CodiceEntita'] = this.entityProperties.codiceEntita();
        nuovo_allegato['Progressivo'] = this.getProgressivo();
        nuovo_allegato['Descrizione'] = this.type;
        nuovo_allegato['Percorso'] = this.entityProperties.percorso().replace(/\s/g, '');
        nuovo_allegato['NomeFile'] = ('allegato_' + new Date().getTime() + type).replace(/\s/g, '');
        nuovo_allegato['MemorizzaContenuto'] = true;

        const reader = new FileReader();
        reader.onerror = () => {
          reader.abort();
          context.detachEntity(nuovo_allegato);
          this.dataManager.saveContext(context);
          console.log('error reading the blob');
        };

        reader.onload = () => {
          nuovo_allegato['File'] = reader.result.split(',')[1];
          this.dataManager.setupAllegato(nuovo_allegato, this.entita);
          this.dataManager.saveContext(context);
        };

        try {
          reader.readAsDataURL(obj);
        } catch (e) {
          reader.abort();
          context.detachEntity(nuovo_allegato);
          this.dataManager.saveContext(context);
          console.log('error reading the blob');
        }

      }
    );
  }

  /* Number of attachments as Progressivo */
  getProgressivo() {
    return this.allEntityAllegati.length + 1;
  }


  is_Empty() {
    Promise.resolve(this.allegati.length === 0);
    // return this.allegati.length === 0 && this.videos.length === 0;
  }

}



