import {ChangeDetectorRef, ViewChild} from '@angular/core';

export abstract class CamComponent {
  @ViewChild('streamView') streamView;
  /* EventEmitter that sends the video */
  stream: MediaStream;
  hidden = true;
  torch_on = false;
  changeDetectorRef: ChangeDetectorRef;
  front_camera_default = false;
  front_camera = this.front_camera_default;
  currConfig = null;
  width = 0;
  height = 0;

  constructor(private ref: ChangeDetectorRef) {
    this.changeDetectorRef = ref;
  }

  /* ngOnInit() {
    this.startCamera();
  } */

  switchCamera() {
    this.stopStreams();
    this.front_camera = !this.front_camera;
    this.startCamera();
    // const facingMode = constraints.facingMode === 'user' ? 'environment' : 'user';
  }

  setupMediaDevices() {
    const browser = <any>navigator;
    const getUserMedia = (browser.getUserMedia || browser.webkitGetUserMedia || browser.mozGetUserMedia || browser.msGetUserMedia);
    if (!browser.mediaDevices || !getUserMedia) {
      alert('Video is not supported');
      return;
    }

    return browser || getUserMedia;
  }

  startCamera() {
    this.hidden = false;
    this.changeDetectorRef.detectChanges();

    this.front_camera = this.front_camera_default;
    this.currConfig = {
      audio: false,
      video: {
        facingMode: (this.front_camera_default ? 'user' : 'environment'),
      }
      /* Better Settings
       width: { min: 1024, ideal: 1280, max: 1920 }
       Front/Rear Camera
       var is_front_camera = false;
       Adjust Framerate
       video: { frameRate: { ideal: 10, max: 15 } }
       */
    };

    this.startStream();
  }

  startStream() {
    this.currConfig.video.facingMode = this.front_camera ? 'user' : 'environment';

    this.setupMediaDevices().mediaDevices.getUserMedia(this.currConfig)
      .then(stream => {
        this.stream = stream;
        const video: HTMLVideoElement = this.streamView.nativeElement;
        video.srcObject = stream;
        video.play();
        this.width = this.streamView.nativeElement.videoWidth;
        this.height = this.streamView.nativeElement.videoHeight;
        this.ref.detach();
      })
      .catch(function (err) {
        console.log(err);
        alert('Permission Denied, ' + err);
      });

  }

  stopStreams() {
    this.stream.getTracks().forEach(track => track.stop());
    this.ref.reattach();
  }

  stopCamera() {
    this.hidden = true;
    this.stopStreams();
  }

  torchAvailable() {
    const capabilities: any = this.stream.getVideoTracks()[0].getCapabilities();
    return capabilities.torch;
  }

  applyConstraint(constraint) {
    try {
      this.stream.getVideoTracks()[0].applyConstraints(constraint);
    } catch (e) {
      console.log(e);
    }
  }

  switchFlash() {
    if (!this.torchAvailable()) {
      return;
    }
    this.torch_on = !this.torch_on;
    this.applyConstraint({advanced: [{'torch': this.torch_on}]});
  }

}






