import {Component, EventEmitter, OnInit, Output, ViewChild} from '@angular/core';
import * as Quagga from 'quagga';
import {NotificationComponent} from '../notification/notification.component';
import {NotificationType} from '../notification/notification-type.enum';


@Component({
  selector: 'app-barcode-reader',
  templateUrl: './barcode-reader.component.html',
  styleUrls: ['./barcode-reader.component.scss']
})

export class BarcodeReaderComponent implements OnInit {
  @ViewChild('notification') notification: NotificationComponent;

  @Output() code_found = new EventEmitter<any>();

  codes: Map<string, number>;

  photo: Blob = null;
  found_code = null;
  hidden = true;
  frontCamera = false;

  ngOnInit() {
    // this.startCamera();
  }

  startCamera() {
    this.codes = new Map<string, number>();
    this.hidden = false;
    const resultCollector = Quagga.ResultCollector.create({
      capture: true, // keep track of the allegato producing this result
      capacity: 1,  // maximum number of results to store
      filter: function (codeResult) {
        // only store results which match this constraint
        // returns true/false
        // e.g.: return codeResult.format === "ean_13";
        return true;
      }
    });
    Quagga.registerResultCollector(resultCollector);

    Quagga.init({
      inputStream: {
        name: 'Live',
        type: 'LiveStream',
        target: document.querySelector('#video_elem'),    // Or '#yourElement' (optional)
        constraints: {
          facingMode: this.frontCamera ? 'user' : 'environment'
        },
      },
      decoder: {
        readers: [
          'code_128_reader',
          'ean_reader',
          'ean_8_reader',
          'code_39_reader',
          'code_39_vin_reader',
          'codabar_reader',
          'upc_reader',
          'upc_e_reader',
          'i2of5_reader',
          '2of5_reader',
          'code_93_reader',
        ],
      }
    }, function (err) {
      if (err) {
        console.log(err);
        return;
      }
      console.log('Initialization finished. Ready to start');

      Quagga.start();
    });
    Quagga.onDetected(data => {
      /* Set Blob and Code variables */
      this.photo = this.dataURLToBlob(resultCollector.getResults()[0]['frame']);
      // this.found_code = data.codeResult.code;
      if (this.codes.has(data.codeResult.code)) {
        this.codes.set(data.codeResult.code, this.codes.get(data.codeResult.code) + 1);
      } else {
        this.codes.set(data.codeResult.code, 0);
      }
      let max = -1;
      this.codes.forEach((value: number, key: string) => {
        if (value > max) {
          max = value;
          this.found_code = key;
        }
      });
      console.log('code: ' + this.found_code + ', times: ' + max);
      /* End */
      if (max >= 20) {
        this.notification.show('Codice riconosciuto', 'Il codice è: ' + this.found_code + '. Corretto?', NotificationType.Confirmation);
        Quagga.pause();
      }
      // console.log(this.photo);
    });
  }

  stopCamera() {
    Quagga.stop();
    this.hidden = true;
  }

  switchCamera() {
    this.stopCamera();
    this.frontCamera = !this.frontCamera;
    this.startCamera();
  }

  dataURLToBlob(dataURL) {
    const BASE64_MARKER = ';base64,';
    if (dataURL.indexOf(BASE64_MARKER) === -1) {
      const parts_aux = dataURL.split(',');
      const contentType_aux = parts_aux[0].split(':')[1];
      const raw_aux = parts_aux[1];

      return new Blob([raw_aux], {type: contentType_aux});
    }

    const parts = dataURL.split(BASE64_MARKER);
    const contentType = parts[0].split(':')[1];
    const raw = window.atob(parts[1]);
    const rawLength = raw.length;

    const uInt8Array = new Uint8Array(rawLength);
    let i;
    for (i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }

    return new Blob([uInt8Array], {type: contentType});
  }

  confirmation(answer: boolean) {
    if (answer) {
      this.code_found.emit({'photo': this.photo, 'code': this.found_code});
      this.stopCamera();
    } else {
      this.codes.clear();
      Quagga.start();
    }
    this.notification.hide();
  }

}



