import {Component, OnInit, ViewChild} from '@angular/core';

@Component({
  selector: 'app-step05-conclusione-autoperizia',
  templateUrl: './step05-conclusione-autoperizia.component.html',
  styleUrls: ['./step05-conclusione-autoperizia.component.scss']
})
export class Step05ConclusioneAutoperiziaComponent implements OnInit {

  optSelected: number;
  @ViewChild('gallery') gallery;
  @ViewChild('yes') yes_radio;

  constructor() {
  }

  change_opt(opt) {
    this.optSelected = opt;
  }

  ngOnInit() {
  }

  set_opt() {
    if (!this.gallery.is_Empty()) {
      this.yes_radio.checked = true;
      this.optSelected = 2;
    }
  }

}



