import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppComponent} from './app.component';
import {PublisherComponent} from './video-component/publisher/publisher.component';
import {SubscriberComponent} from './video-component/subscriber/subscriber.component';
import {OpentokService} from './services/opentok.service';
import {ServiceWorkerModule} from '@angular/service-worker';
import {environment} from '../environments/environment';
import {VideoChatComponent} from './video-component/video-chat.component';
import {routes} from './app.routing';
import {RouterModule, Routes} from '@angular/router';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import {BreezeBridgeHttpClientModule} from 'breeze-bridge2-angular';
import {HttpClientModule} from '@angular/common/http';

import 'hammerjs';
import {
  MatButtonModule,
  MatCardModule, MatDatepickerModule,
  MatFormFieldModule,
  MatIconModule, MatInputModule,
  MatMenuModule, MatNativeDateModule, MatOptionModule,
  MatSelectModule, MatStepperModule, MatToolbarModule, MatRadioModule
} from '@angular/material';
import {MenudemoComponent} from './menudemo/menudemo.component';
import {GeoLocationService} from './services/geo-location.service';
import {DataService} from './services/dataService/data.service';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {AutoperiziaComponent} from './autoperizia/autoperizia.component';
import {NavbarComponent} from './navbar/navbar.component';
import {PageBackgroundComponent} from './page-background/page-background.component';
import {LoginComponent} from './login/login.component';
import {NO_ERRORS_SCHEMA} from '@angular/core';
import {MDBBootstrapModule} from 'angular-bootstrap-md';
import {VideoComponent} from './cam/video/video.component';
import {PhotoComponent} from './cam/photo/photo.component';
import {AngularFontAwesomeModule} from 'angular-font-awesome';
import {AgmCoreModule, GoogleMapsAPIWrapper} from '@agm/core';
import {CookieOptions, CookieService} from 'angular2-cookie/core';
import {BarcodeReaderComponent} from './barcodeReader/barcode-reader.component';
import {HttpModule} from '@angular/http';
import {NgForageConfig, NgForageModule} from 'ngforage';
import {ChooserComponent} from './chooser/chooser.component';
import {NotificationComponent} from './notification/notification.component';
import {CheckRequirementsComponent} from './check-requirements/check-requirements.component';
import {DlDateTimePickerDateModule} from 'angular-bootstrap-datetimepicker';
import {Step01DataSinistroComponent} from './autoperizia/step01-data-sinistro/step01-data-sinistro.component';
import {DataManagerService} from './services/data-manager.service';
import {AutoperiziaUtilitiesService} from './services/autoperizia-utilities.service';
import {Step02CausaSinistroComponent} from './autoperizia/step02-causa-sinistro/step02-causa-sinistro.component';
import {Step03MacrogruppoDannoComponent} from './autoperizia/step03-macrogruppo-danno/step03-macrogruppo-danno.component';
import {Step03aCategoriaBeneComponent} from './autoperizia/step03a-categoria-bene/step03a-categoria-bene.component';
import {Step03bDettaglioBeneComponent} from './autoperizia/step03b-dettaglio-bene/step03b-dettaglio-bene.component';
import {Step03cParticolareDannoComponent} from './autoperizia/step03c-particolare-danno/step03c-particolare-danno.component';
import {Step04AltriDanniComponent} from './autoperizia/step04-altri-danni/step04-altri-danni.component';
import {Step05ConclusioneAutoperiziaComponent} from './autoperizia/step05-conclusione-autoperizia/step05-conclusione-autoperizia.component';
import {Step06InformazioniFinaliComponent} from './autoperizia/step06-informazioni-finali/step06-informazioni-finali.component';
import {GalleryComponent} from './gallery/gallery.component';
import {SlickModule} from 'ngx-slick';
import {AppointmentComponent} from './appointment/appointment.component';

@NgModule({
  declarations: [
    AppComponent,
    PublisherComponent,
    SubscriberComponent,
    VideoChatComponent,
    MenudemoComponent,
    AutoperiziaComponent,
    NavbarComponent,
    PageBackgroundComponent,
    LoginComponent,
    VideoComponent,
    PhotoComponent,
    BarcodeReaderComponent,
    ChooserComponent,
    NotificationComponent,
    CheckRequirementsComponent,
    Step01DataSinistroComponent,
    Step02CausaSinistroComponent,
    Step03MacrogruppoDannoComponent,
    Step03aCategoriaBeneComponent,
    Step03bDettaglioBeneComponent,
    Step03cParticolareDannoComponent,
    GalleryComponent,
    AppointmentComponent,
    Step05ConclusioneAutoperiziaComponent,
    Step06InformazioniFinaliComponent,
    Step04AltriDanniComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MatMenuModule,
    MatIconModule,
    MatCardModule,
    MatFormFieldModule,
    MatSelectModule,
    MatOptionModule,
    MatInputModule,
    MatButtonModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatToolbarModule,
    MatRadioModule,
    FormsModule,
    ReactiveFormsModule,
    MatStepperModule,
    BreezeBridgeHttpClientModule,
    AngularFontAwesomeModule,
    HttpClientModule,
    HttpModule,
    NgForageModule.forRoot(),
    RouterModule.forRoot(routes),
    MDBBootstrapModule.forRoot(),
    SlickModule.forRoot(),
    DlDateTimePickerDateModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyBQYkAUwy-POjiFa7UC9lUMws1BosPzXek'
    }),
    environment.production ? ServiceWorkerModule.register('/LiquidaOnline/ngsw-worker.js') : []
  ],
  providers: [
    OpentokService,
    GeoLocationService,
    DataService,
    GoogleMapsAPIWrapper,
    CookieService,
    DataManagerService,
    AutoperiziaUtilitiesService,
    {provide: CookieOptions, useValue: {}}
  ],
  bootstrap: [AppComponent],
  schemas: [NO_ERRORS_SCHEMA],
  exports: [RouterModule]
})
export class AppModule {
  public constructor(ngfConfig: NgForageConfig) {
    ngfConfig.configure({
      name: 'LiquidaOnline',
      driver: [ // defaults to indexedDB -> webSQL -> localStorage -> sessionStorage
        NgForageConfig.DRIVER_INDEXEDDB,
        NgForageConfig.DRIVER_LOCALSTORAGE
      ]
    });
  }
}



