import {AfterViewInit, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {NotificationType} from './notification-type.enum';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss']
})
export class NotificationComponent implements AfterViewInit {

  @ViewChild('notificationModal') public notificationModal: any;
  // @ViewChild('uploadSuccessModal') public showSuccessModal: any;
  // @ViewChild('uploadFailModal') public showFailModal: any;
  // @ViewChild('notificationModal') public showNotificationModal: any;

  @Output() onClick: EventEmitter<any> = new EventEmitter();
  @Output() shown: EventEmitter<any> = new EventEmitter();
  @Output() hidden: EventEmitter<any> = new EventEmitter();

  title: string;
  message: string;
  icon: string;
  color: string;
  form: boolean;

  backdropClasses = 'd-none';

  constructor(private ref: ChangeDetectorRef) {
  }

  ngAfterViewInit() {
  }

  show(title: string, message: string, type: NotificationType) {
    this.title = title;
    this.message = message;
    this.form = false;
    switch (type) {
      case NotificationType.Success:
        this.icon = 'fa-check-square';
        this.color = 'green';
        break;
      case NotificationType.Error:
        this.icon = 'fa-exclamation-circle';
        this.color = 'red';
        break;
      case NotificationType.Confirmation:
        this.icon = 'fa-question-circle';
        this.color = 'yellow';
        this.form = true;
        break;
      default:
        this.icon = 'fa-bell';
        this.color = 'light-blue';
    }
    console.log(this.icon);
    console.log(this.color);
    this.notificationModal.show();
    this.ref.detectChanges();

  }

  _shown() {
    this.shown.emit();
  }

  _hidden() {
    this.removeBackdrop();
    this.hidden.emit();
  }

  fadeOutBackdrop() {
    this.backdropClasses = 'out hide';
  }

  fadeInBackdrop() {
    this.backdropClasses = 'in show';
  }

  removeBackdrop() {
    this.backdropClasses = 'd-none';
  }

  hide() {
    this.notificationModal.hide();
  }

  success() {
    this.onClick.emit(true);
  }

  fail() {
    this.onClick.emit(false);
  }
}



