import {Component, OnInit, ChangeDetectorRef, Input, AfterViewInit, ViewChild, ElementRef, OnDestroy} from '@angular/core';
import { OpentokService } from '../services/opentok.service';
import * as OT from '@opentok/client';
import {DataService} from '../services/dataService/data.service';
import {GeoLocationService} from '../services/geo-location.service';
import {CookieService} from 'angular2-cookie/core';
import {Router} from '@angular/router';
import {InputDecorator} from '@angular/core/src/metadata/directives';
import {PhotoComponent} from '../cam/photo/photo.component';
import {config as breezeConfig, EntityManager} from 'breeze-client';
import config from '../../config';
import {NgForage, NgForageCache} from 'ngforage';
import {NotificationComponent} from '../notification/notification.component';
import {NotificationType} from '../notification/notification-type.enum';
import {ISubscription} from 'rxjs/Subscription';

@Component({
  selector: 'app-video-chat',
  templateUrl: './video-chat.component.html',
  styleUrls: ['./video-chat.component.css']
})
export class VideoChatComponent implements AfterViewInit, OnDestroy {
  @ViewChild('placeholderDiv') placeholderDiv: ElementRef;
  @ViewChild('loadingDiv') loadingDiv: ElementRef;
  @ViewChild('notification') notification: NotificationComponent;

  title = 'Video Component';
  session: OT.Session;
  streams: Array<OT.Stream> = [];
  changeDetectorRef: ChangeDetectorRef;
  lat: 0;
  long: 0;
  address = 'Ti stiamo geolocalizzando..';
  appointmentInterval: any;
  locationSubscription: ISubscription;

  constructor(private ref: ChangeDetectorRef, private opentokService: OpentokService,
              private geoLocationService: GeoLocationService, private dataService: DataService,
              private cookieService: CookieService, private router: Router,
              private readonly ngf: NgForage, private readonly cache: NgForageCache) {
    this.changeDetectorRef = ref;
    if (this.cookieService.get('token') === undefined || this.cookieService.get('PIN') === undefined) {
      router.navigateByUrl('login');
      return;
    }
    this.dataService.checkAndAddAuthorizationTokenToBreeze(this.cookieService.get('token'));
    this.appointmentInterval = setInterval(() => {
      if (this.streams.length === 0) {
        this.notification.show('Nessun operatore disponibile',
          'Desideri fissare un appuntamento?',
          NotificationType.Confirmation);
      }
    }, 120000);
  }

  confirmation(answer: boolean) {
    if (answer) {
      this.router.navigateByUrl('appointment');
    }
    this.notification.hide();
  }

  ngOnDestroy() {
    clearInterval(this.appointmentInterval);
    this.locationSubscription.unsubscribe();
    this.session.disconnect();
  }

  ngAfterViewInit () {
    this.placeholderDiv.nativeElement.style.width = (window.screen.width * 0.9).toString() + 'px';
    this.placeholderDiv.nativeElement.style.height = (window.screen.width * 0.9 * 9 / 16).toString() + 'px';

    const context: EntityManager = new EntityManager(config.SERVER_SVILUPPO);
    this.dataService.getEntitiesParamBinary('GetDossierNominativoByPin', ({ pin: this.cookieService.get('PIN') }), context,
    dossier => {
      console.log(dossier[0]);
      this.ngf.setItem('videoContext', context.exportEntities()).then(s => {
        this.opentokService.initSession(this.cookieService.get('PIN')).then((session: OT.Session) => {
          this.session = session;
          this.session.on('streamCreated', (event) => {
            this.streams.push(event.stream);
            this.changeDetectorRef.detectChanges();
          });
          this.session.on('streamDestroyed', (event) => {
            const idx = this.streams.indexOf(event.stream);
            if (idx > -1) {
              this.streams.splice(idx, 1);
              this.changeDetectorRef.detectChanges();
            }
          });
        })
          .then(() => this.opentokService.connect())
          .catch((err) => {
            console.error(err);
            alert('Unable to connect to the room');
          });
      });
    }, error => {
        console.log(error);
        if (error.status === 401) {
          this.cookieService.removeAll();
          this.router.navigateByUrl('/login');
        }
    });

    const options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0,
      desiredAccuracy: 0,
      frequency: 1
    };
    const source = this.geoLocationService.getLocation(options);
    this.locationSubscription = source.subscribe(pos => {
      const self = this;
      console.log(navigator.geolocation);
      this.geoLocationService.getAddress(pos.coords.latitude, pos.coords.longitude, (newAddress: string) => {
        self.lat = pos.coords.latitude;
        self.long = pos.coords.longitude;
        self.address = newAddress;
        console.log(self.address);
      });
    }, err => {
      console.log(err);
    });
    // this.loadingDiv.nativeElement.style.innerPadding = (window.screen.width * 0.9 * 9 / 16 / 2) + 'px';
    // this.placeholderDiv.nativeElement.style.paddingBottom = (window.screen.width * 0.9 * 9 / 16 / 2) + 'px';
    // document.getElementById('placeholder').style.width =  (window.screen.width * 0.6).toString() + 'px';
    // document.getElementById('placeholder').style.height =  (window.screen.width * 0.4).toString() + 'px';
  }
}



