import {Injectable} from '@angular/core';
import {DataService} from './dataService/data.service';
import {CookieService} from 'angular2-cookie/core';
import config from '../../config';
import {config as breezeConfig, Entity, EntityManager} from 'breeze-client';
import {NgForage} from 'ngforage';
import {DomSanitizer} from '@angular/platform-browser';

@Injectable()
export class DataManagerService {

  constructor(private dataService: DataService, private cookieService: CookieService, private ngf: NgForage,
              private sanitizer: DomSanitizer) {
    const tk = this.cookieService.get('token');
    if (tk) {
      this.setTokenBreezeHeader(tk);
    }

  }

  loginDownloaded = false;
  contextPromise: Promise<EntityManager> = null;
  savePromise = null;
  isChanged = false;

  private setTokenBreezeHeader(token: string) {
    const ajaxAdapter: any = breezeConfig.getAdapterInstance('ajax');
    // set fixed headers
    ajaxAdapter.defaultSettings = {
      headers: {
        'Authorization': token
      }
    };
  }


  /**
   * Serializes saveContext calls and if nothing changed does not save entities in local context
   *
   * @param {EntityManager} context
   * @returns {any}
   */
  saveContext(context: EntityManager) {

    // Saved pointer used to identiy the promises and set the done property
    const localSavePromise = this.savePromise;

    // Clean the promises list when all the previous promises have resolver
    if (localSavePromise === null || localSavePromise.done) {
      // this.savePromise = context.saveChanges();
    } else {
      // If not all the promises revoled then add yourself to the list
      this.savePromise = this.savePromise.then(() => {
        localSavePromise.done = true;
        //  return context.saveChanges();
      });
    }
    // Only need to export the context to the local db if something changed
    if (!this.isChanged) {
      return new Promise(resolve => resolve());
    }

    this.isChanged = false;
    return this.ngf.setItem(this.getPIN(), context.exportEntities());

  }

  getContext() {
    if (!this.contextPromise) {
      this.contextPromise = this._privateGetContext();
    }

    return this.contextPromise;
  }

  private _privateGetContext() {
    const promises = [];

    const context = new EntityManager(config.SERVER_SVILUPPO);

    context.entityChanged.subscribe(() => {
      this.isChanged = true;
    });

    if (!this.loginDownloaded) {
      promises.push(this.afterLoginDownload(context));
    }

    let prom = this.metadata_getContext(context);
    promises.push(prom);

    prom = this.cache_getContext(context);
    promises.push(prom);

    return Promise.all(promises).then(() => {
      return this.createBaseEntities(context).then(() => {
        return this.saveContext(context).then(() => context);
      });
    });
  }

  private metadata_getContext(context: EntityManager) {
    return this.ngf.getItem('metadata').then(data => {

      if (data) {
        context.metadataStore.importMetadata(data.toString());
      } else {
        return context.fetchMetadata(() => {
          this.ngf.setItem('metadata', context.metadataStore.exportMetadata());
        });
      }
    });
  }

  private cache_getContext(context) {
    return this.ngf.getItem(this.getPIN()).then(data => {
        if (data) {
          context.importEntities(data);
          // Mapping db key(name) to array of entities for that name
          const entita = {};
          // Array of mapping from db key(name) to entity ID field name
          const entitiesKeyFieldName = {};
          entita['DOSSIERBENE_EXT'] = this.getDossierBeni(context);
          entitiesKeyFieldName['DOSSIERBENE_EXT'] = 'ID';
          entita['DOSSIERNOMINATIVO'] = context.getEntities('DossierNominativoView');
          entitiesKeyFieldName['DOSSIERNOMINATIVO'] = 'IDDossierNominativo';

          const allegati = this.getAllegati(context);
          // Used to store allegati divided by db key(name) of entities
          const allegatiPerType = {};

          allegati.forEach(allegato => {
            const type = allegato['TipoEntita'];
            if (!allegatiPerType[type]) {
              allegatiPerType[type] = [];
            }
            allegatiPerType[type].push(allegato);
          });

          // For each registered db key(name) add allegati array to entities by matching owner entities ID in allegato
          for (const entityType of Object.keys(allegatiPerType)) {
            if (!entita[entityType]) {
              throw new Error('Entity not expected to have allegati');
            }

            entita[entityType].forEach(singleEntity => {
              singleEntity['custom_Allegati'] = {};
              singleEntity['custom_Allegati']['custom_all'] = [];
              const entityKeyFieldName = entitiesKeyFieldName[entityType];

              allegati.filter(allegato => allegato['IDEntita'] === singleEntity[entityKeyFieldName])
                .forEach(allegato => this.setupAllegato(allegato, singleEntity));
            });
          }
        }
      }
    );
  }

  // Sets up allegato custom fields used in initialization and when adding an allegato from gallery
  setupAllegato(allegato: Entity, owner: Entity) {
    allegato['custom_extension'] = '.' + allegato['NomeFile'].split('.')[1];
    let prefix = '';
    let isVideo = false;
    switch (allegato['custom_extension']) {
      case '.png':
        prefix = 'allegato/png';
        break;
      case '.gif':
        prefix = 'allegato/gif';
        isVideo = true;
        break;
      case '.webm':
        prefix = 'video/webm';
        isVideo = true;
        break;
    }
    prefix = 'data:' + prefix + ';base64,';
    const win: any = window;
    const blob = this.dataURLToBlob(prefix + allegato['File']);
    const url = (win.URL || win.webkitURL).createObjectURL(blob);
    allegato['custom_url'] = this.sanitizer.bypassSecurityTrustUrl(url);
    allegato['custom_url_css'] = this.sanitizer.bypassSecurityTrustStyle('url(' + url + ')');
    allegato['custom_isVideo'] = isVideo;

    allegato['custom_owner'] = owner;
    if (!owner['custom_Allegati'][allegato['Descrizione']]) {
      owner['custom_Allegati'][allegato['Descrizione']] = [];
    }

    owner['custom_Allegati'][allegato['Descrizione']].push(allegato);
    owner['custom_Allegati']['custom_all'].push(allegato);
  }

  dataURLToBlob(dataURL) {
    const BASE64_MARKER = ';base64,';
    if (dataURL.indexOf(BASE64_MARKER) === -1) {
      const parts_aux = dataURL.split(',');
      const contentType_aux = parts_aux[0].split(':')[1];
      const raw_aux = parts_aux[1];

      return new Blob([raw_aux], {type: contentType_aux});
    }

    const parts = dataURL.split(BASE64_MARKER);
    const contentType = parts[0].split(':')[1];
    const raw = window.atob(parts[1]);
    const rawLength = raw.length;

    const uInt8Array = new Uint8Array(rawLength);
    let i;
    for (i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }

    return new Blob([uInt8Array], {type: contentType});
  }


  private afterLoginDownload(context: EntityManager) {
    const nothing = () => null;
    const promises = [];

    let prom = this.dataService.getEntitiesParamBinary('GetDossierNominativoByPin', ({pin: this.getPIN()}), context,
      nothing, nothing);
    promises.push(prom);

    prom = this.dataService.getEntities('CauseSinistroAutoPerizia', context, nothing, nothing);
    promises.push(prom);

    prom = this.dataService.getEntities('PartiteAutoPerizia', context, nothing, nothing);

    const promT = prom.then(() => {
      const partite = context.getEntities('Partita');
      const partitePromises = partite.map(partita => {
        const partitaObj = {idPartita: partita['ID']};
        const innerProm = this.dataService.getEntitiesParamBinary('ModelliAutoPerizia', (partitaObj), context, nothing, nothing);
        const innerProm2 = this.dataService.getEntitiesParamBinary('ModelloVariantiAutoPerizia', (partitaObj), context, nothing, nothing);

        return innerProm.then(() => {
          partita['custom_Modelli'] = context.getEntities('Modello').filter(d => d['IDTipoModello'] === partita['ID']);
          return innerProm2;
        });
      });

      return Promise.all(partitePromises);
    });

    promises.push(promT);

    prom = this.dataService.getEntities('LivelliDanno', context, nothing, nothing);
    promises.push(prom);
    return Promise.all(promises).then(() => {
      this.loginDownloaded = true;
    });
  }

  private createBaseEntities(context: EntityManager) {
    return this.getOrAddDossierPerizia(context).then(() => {
      if (this.getDossierBeni(context).length === 0) {
        this.addDossierBene(context);
      }
    });

    /**
     *
     */
  }

  getDossierPerizia(context: EntityManager) {

    return context.getEntities('DossierPerizia_Ext')[0];
  }

  getOrAddDossierPerizia(context: EntityManager) {
    console.log('calling');

    const dossierPerizia = this.getDossierPerizia(context);
    console.log(dossierPerizia);
    if (!dossierPerizia) {
      console.log('fetching');
      const nothing = () => null;
      const promise = this.dataService.getEntitiesParamBinary('GetDossierPeriziaByPIN', ({'pin': this.getPIN()}),
        context, nothing, nothing);
      return promise.then(() => {
        const res = this.getDossierPerizia(context);
        if (res) {
          console.log(res);
          return new Promise(resolve => resolve(() => res));
        }

        const dosssier = this.dataService.addEntity('DossierPerizia_Ext', {}, context);
        context.attachEntity(dosssier);

        const nominativo = context.getEntities('DossierNominativoView')[0];
        dossierPerizia['IDDossierNominativo'] = nominativo['IDDossierNominativo'];
        return this.saveContext(context).then(() => dosssier);
      });
    }

    return new Promise(resolve => resolve(dossierPerizia));
  }

  addDossierGaranzia(context: EntityManager, partita: Entity) {

    const dossierPerizia = this.getDossierPerizia(context);

    const params = {
      'DossierPerizia_Ext': dossierPerizia,
      'Partita': partita,
    };

    const garanzia = this.dataService.addEntity('DossierGaranzia_Ext', params, context);
    context.addEntity(garanzia);
    return garanzia;
  }

  getDossierBeni(context: EntityManager) {
    return context.getEntities('DossierBene_Ext');
  }

  addDossierBene(context: EntityManager) {
    const dossier = this.dataService.addEntity('DossierBene_Ext', {}, context);
    context.addEntity(dossier);
    this.saveContext(context);
    return dossier;
  }

  getPIN() {
    return this.cookieService.get('PIN');
  }

  addAllegato(context: EntityManager) {
    const allegato = this.dataService.addEntity('Allegato', {}, context);
    context.addEntity(allegato);
    this.saveContext(context);
    return allegato;
  }

  getAllegati(context: EntityManager) {
    return context.getEntities('Allegato');
  }

  getNestedProperty(entity: Entity, keys: Array<string>, def = null) {
    let currLevel: any = entity;

    for (const key of keys) {
      currLevel = currLevel[key];
      if (!currLevel) {
        return def;
      }
    }
    return currLevel;

  }
}



