import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {DataManagerService} from '../../services/data-manager.service';
import {Entity} from 'breeze-client';

@Component({
  selector: 'app-step03-macrogruppo-danno',
  templateUrl: './step03-macrogruppo-danno.component.html',
  styleUrls: ['./step03-macrogruppo-danno.component.scss']
})
export class Step03MacrogruppoDannoComponent implements OnInit {
  @Input() dossierBene: Entity;
  @Output() partitaChanged = new EventEmitter();
  elencoPartite = [];
  selectedPartita = null;

  constructor(private dataManager: DataManagerService) {
  }

  confirmChanges() {
    if (!this.selectedPartita) {
      return;
    }

    this.dataManager.getContext().then(context => {
      let garanzia = this.selectedPartita['DossierGaranzie_Ext'][0];
      if (!garanzia) {
        garanzia = this.dataManager.addDossierGaranzia(context, this.selectedPartita);
      }
      this.dossierBene['DossierGaranzia_Ext'] = garanzia;
      this.dataManager.saveContext(context).then(() => this.partitaChanged.emit());
    });
  }


  ngOnInit(): void {
    this.dataManager.getContext().then(context => {
      this.elencoPartite = context.getEntities('Partita');
      this.selectedPartita = this.dataManager.getNestedProperty(this.dossierBene, ['DossierGaranzia_Ext', 'Partita'], null);
    });
  }

}



