import {AfterViewInit, Component, ViewChild} from '@angular/core';
import {DataService} from '../services/dataService/data.service';
import config from '../../config';
import {EntityManager, config as breezeConfig, FilterQueryOp, Entity} from 'breeze-client';
import {BizFilter} from '../services/dataService/bizFilter';
import {BizStream} from '../services/dataService/bizStream';
import {ActivatedRoute, Router} from '@angular/router';
import {CookieService} from 'angular2-cookie/core';
import {NgForage, NgForageCache} from 'ngforage';
import {NotificationType} from '../notification/notification-type.enum';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements AfterViewInit {

  @ViewChild('loginModal') public showModalOnClick: any;
  @ViewChild('notification') notificationModal: any;

  public showModal(): void {
    this.showModalOnClick.show();
  }

  public hideModal(): void {
    this.showModalOnClick.hide();
  }

  private setTokenBreezeHeader(token: string) {
    const ajaxAdapter: any = breezeConfig.getAdapterInstance('ajax');
    // set fixed headers
    ajaxAdapter.defaultSettings = {
      headers: {
        'Authorization': token
      }
    };
  }

  constructor(private dataService: DataService, private router: Router, private cookieService: CookieService, private readonly ngf: NgForage, private readonly cache: NgForageCache) {
    console.log(this.cookieService.get('token'));

  }

  ngAfterViewInit() {
    if (this.cookieService.get('token') !== undefined) {
      this.setTokenBreezeHeader(this.cookieService.get('token'));
      this.router.navigateByUrl('chooser');
    } else {
      this.showModal();
    }

  }

  login(name: string, surname: string, PIN: number) {
    console.log('login function called with parameters: ' + name + ', ' + surname + ', ' + PIN);
    this.notificationModal.show(
      'Upload delle informazioni',
      'Si prega di attendere mentre il servizio verifica le credenziali inserite',
      NotificationType.Information);
    const context: EntityManager = new EntityManager(config.SERVER_LOGIN_SVILUPPO);
    // const context1: EntityManager = new EntityManager(config.SERVER_SVILUPPO);

    this.dataService.getEntitiesParamBinary(
      'LoginGuest',
      ({nome: name, cognome: surname, PIN: PIN}),
      context, data => {
        console.log('Callback started');
        // get the current default Breeze AJAX adapter
        // const ajaxAdapter: any = breezeConfig.getAdapterInstance('ajax');
        console.log(data.length);

        // const guestIdentity = data[0].result;

        // set fixed headers
        /*
        ajaxAdapter.defaultSettings = {
          headers: {
            'Authorization': data[0]['Token']
          }
        };
        */

        if (data.length === 0) {
          this.notificationModal.hide();
          this.notificationModal.show(
            'Errore di autenticazione',
            'Le credenziali inserite non sono valide. Controllare di avere digitato correttamente il PIN',
            NotificationType.Error);
          setTimeout(() => {
            this.notificationModal.hide();
          }, 4000);
          return;
        }

        this.dataService.checkAndAddAuthorizationTokenToBreeze(data[0]['Token']);

        this.cookieService.put('token', data[0]['Token']);
        this.cookieService.put('PIN', PIN.toString());

        this.hideModal();
        this.notificationModal.hide();

        /*this.dataService.getEntitiesParamBinary('GetDossierNominativoByPin', ({ pin: PIN }), context1,
            dossier => {
          console.log(dossier[0]);
          this.createAttachment(context1, dossier[0]);
        }, error => {
         console.log(error);
        });*/
        console.log('redirect to chooser');
        this.router.navigateByUrl('chooser');

      }, error => {
        console.log(error);
        // to-do
      });
  }

  createAttachment(context: EntityManager, nominativo: Entity) {
    console.log('createAttachment called');
    /* Livello per video-perizia */
    const path = 'UploadFile/' + 'DOSSIER/' + nominativo['Riferimento'] + '/DOSSIERNOMINATIVO/' + nominativo['Nominativo'];
    console.log('path created: ' + path);
    const attachment: Entity = this.dataService.addEntity('Allegato', {}, context);
    attachment['TipoEntita'] = 'DOSSIERNOMINATIVO';
    attachment['IDEntita'] = nominativo['IDDossierNominativo'];
    attachment['Descrizione'] = 'testo libero';
    attachment['NomeFile'] = 'filename';
    attachment['DataGPS'] = new Date();
    attachment['Percorso'] = path;
    attachment['Latitudine'] = 23.989878;
    attachment['Longitudine'] = 97.989867;
    attachment['IDTipoAllegato'] = 2;
    attachment['Progressivo'] = 1;
    attachment['CodiceEntita'] = nominativo['Nominativo'];
    attachment['MemorizzaContenuto'] = true;
    /* sempre false - così cancella blob e salvafile su filesystem */
    /* attachment['Barcode'] = 'agdshdfdshg'; */
    /* attachment['File'] = binary_image (blob) - [1,5] MB max*/
    console.log('attachment created');
    context.saveChanges().then(success => {
      this.ngf.setItem('newTestBreeze', context.exportEntities()).then(s => {
        const context1: EntityManager = new EntityManager(config.SERVER_SVILUPPO);
        this.ngf.getItem('newTestBreeze').then(data => context1.importEntities(data));
        // context1.saveChanges();
        console.log(context1);
      });
      console.log('attachment saved');

      console.log('createAttachment finished');
    }).catch(error => {
      console.log('My message: ' + error);
    });

  }

}




