///<reference path="../../../../node_modules/@angular/core/src/metadata/directives.d.ts"/>
import {AfterViewInit, ChangeDetectorRef, Component, EventEmitter, NgZone, Output, QueryList, ViewChild, ViewChildren} from '@angular/core';
import {CamComponent} from '../cam.component';
import {DomSanitizer} from '@angular/platform-browser';
import {NotificationType} from '../../notification/notification-type.enum';

declare var MediaRecorder: any;
declare var GIF: any;
declare var navigator: any;
declare var window: any;

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss']

})

export class VideoComponent extends CamComponent implements AfterViewInit {
  @ViewChild('videoPreviewElement') videoPreviewElement;
  @ViewChildren('canvas') canvas;
  @ViewChild('gifPreview') gifPreview;
  @ViewChild('notification') notification;

  /* EventEmitter that sends the video */
  @Output() video_done = new EventEmitter<any>();
  @Output() previewAvailable = new EventEmitter<any>();

  constructor(private ref1: ChangeDetectorRef, private sanitizer: DomSanitizer, private _zone: NgZone) {
    super(ref1);

    this.isGif = navigator.getUserMedia ||
      navigator.webkitGetUserMedia ||
      navigator.mozGetUserMedia ||
      navigator.msGetUserMedia ||
      window.RTCPeerConnection;

    this.isGif = !this.isGif;
  }


  /*
      Adjustable settings
   */
  video_length = 5; // length of chunks in ms
  chunk_length = 1000;
  /*
     END of Adjustable settings
  */
  stream: MediaStream;
  video: Blob = null;
  gifVideo = null;
  timer = this.video_length;

  recording = false;
  preview_hidden = true;
  gif_preview_hidden = true;

  mediaRecorder: any;
  chunks = [];
  width = 0;
  height = 0;
  currFrame = 0;
  encoder = null;
  context = null;
  fps = 18;
  maxW = 600;
  maxH = 480;
  extension = '';
  startTimestamp = null;
  isGif = false;
  snapshotInterval = null;
  totalFrames = this.fps * this.video_length;
  canvasesBind = null;
  contexts = null;

  static numberPadding(num) {
    return num >= 10 ? num : '0' + num;
  }

  ngAfterViewInit(): void {

  }

  startCamera() {
    this.preview_hidden = true;
    this.gif_preview_hidden = true;
    this.timer = this.video_length;
    super.startCamera();
  }

  startTakingVideo() {

    this.isGif = false;
    this.extension = '.webm';
    this.recording = true;

    this.chunks = [];
    // Timer Init
    this.timer = this.video_length;
    this.video = null;

    this.mediaRecorder = new MediaRecorder(this.stream);

    this.mediaRecorder.ondataavailable = (e) => this.pushChunk(e);
    this.mediaRecorder.onstop = () => {
      this.video = new Blob(this.chunks, {'type': this.mediaRecorder.mimeType});
      this.videoFinished();
    };

    this.mediaRecorder.start(this.chunk_length);
    // The MediaRecorder state must be 'recording'
  }

  startTakingGif() {

    this.isGif = true;
    this.extension = '.gif';
    this.recording = true;


    this.currFrame = 0;
    this.width = this.streamView.nativeElement.videoWidth;
    this.height = this.streamView.nativeElement.videoHeight;
    this.startTimestamp = Date.now();

    const r = this.width / this.height;

    if (this.width > this.maxW) {
      this.width = this.maxW;
      this.height = this.width / r;
    }

    if (this.height > this.maxH) {
      this.height = this.maxH;
      this.width = this.height * r;
    }
    // this.canvas.nativeElement.width = this.width;
    // this.canvas.nativeElement.height = this.height;

    // this.context = this.canvas.nativeElement.getContext('2d');
    this.changeDetectorRef.detectChanges();

    if (!this.contexts) {
      this.canvasesBind = Array.from(new Array(this.totalFrames + 1), (x, i) => i);
      this.ref1.detectChanges();
      this.contexts = this.canvas.map(canvas => canvas.nativeElement.getContext('2d'));
    }

    this._zone.runOutsideAngular(() => {
      this.snapshotInterval = setInterval(() => this.takeSnapshot(), 1000 / this.fps);
    });
  }

  startTakingAutomaticVideo() {
    if (!this.stream) {
      return;
    }
    this.video = null;
    this.gifVideo = '';
    if (this.isGif) {
      this.extension = '.gif';
      this.startTakingGif();
    } else {
      this.extension = '.webm';
      this.startTakingVideo();
    }
  }

  modalShown() {


    this.encoder = new GIF({
      workers: 18,
      quality: 20,
      workerScript: 'assets/gif.worker.js'
    });
    this.encoder.on('finished', blob => {
      this.video = blob;
      this.contexts.forEach(context => context.clearRect(0, 0, this.width, this.height));
      this.videoFinished();
      this.notification.hide();
    });

    for (let i = 0; i !== this.currFrame; i++) {
      this.encoder.addFrame(this.canvas._results[i].nativeElement, {
        copy: false,
        delay: 1000 / this.fps,
        dither: 'Atkinson'
      });

    }

    this.encoder.render();
  }

  takeSnapshot() {
    this.currFrame++;
    const elapsedTime = (Date.now() - this.startTimestamp) / 1000;
    const oldTimer = this.timer;
    this.timer = Math.floor(this.video_length - elapsedTime);
    this.timer = VideoComponent.numberPadding(this.timer);

    if (oldTimer !== this.timer) {
      this.ref1.detectChanges();
    }

    if (this.timer <= 0 && this.recording) {
      this.timer = VideoComponent.numberPadding(0);

      this.stopStreams();
      clearInterval(this.snapshotInterval);
      this.notification.show('Caricamento in corso', 'Attendere, caricamento del video', NotificationType.Information);

      return;
    }

    this.contexts[this.currFrame].drawImage(this.streamView.nativeElement, 0, 0, this.width, this.height);
  }

  pushChunk(chunk) {
    if (this.timer === 0) {
      return;
    }
    /* Update Timer */
    this.timer--;
    /* End Update */
    this.chunks.push(chunk.data);
    /* Check if time is over */
    if (this.timer === 0) {
      this.stopTakingVideo();
    }
    this.timer = VideoComponent.numberPadding(this.timer);
    this.changeDetectorRef.detectChanges();

    /* End check */
  }

  videoFinished() {
    this.previewAvailable.emit({video: this.video, type: this.extension});
    this.stopStreams();
    this.recording = false;
    if (this.isGif) {
      this.showGifPreview();
    } else {
      this.showVideoPreview();
    }
    this.changeDetectorRef.detectChanges();
  }

  showVideoPreview() {
    this.preview_hidden = false;
    this.gif_preview_hidden = true;
    this.videoPreviewElement.nativeElement.src = URL.createObjectURL(this.video);
    this.videoPreviewElement.nativeElement.load();
    /* Show Preview */
    this.videoPreviewElement.nativeElement.onloadeddata = () => this.videoPreviewElement.nativeElement.play();
  }

  showGifPreview() {
    this.preview_hidden = false;
    this.gif_preview_hidden = false;

    this.gifVideo = 'url(' + URL.createObjectURL(this.video) + ')';
    this.gifVideo = this.sanitizer.bypassSecurityTrustStyle(this.gifVideo);

  }

  acceptPreview() {
    if (!this.video) {
      return;
    }

    this.stopCamera();
    this.video_done.emit({video: this.video, type: this.extension});
  }

  rejectPreview() {
    if (!this.video) {
      return;
    }

    this.preview_hidden = true;
    this.gif_preview_hidden = true;
    this.startCamera();
  }


  interruptVideo() {
    if (!this.isGif) {
      this.mediaRecorder.onstop = null;
    }
    this.stopTakingVideo();
  }

  stopTakingVideo() {
    this.recording = false;
    this.timer = this.video_length;

    if (!this.isGif) {
      this.mediaRecorder.stop();
    }
  }


}




