import { Component, Input, OnChanges, SimpleChanges} from '@angular/core';
import {DataManagerService} from '../../services/data-manager.service';
import {Entity} from 'breeze-client';

@Component({
  selector: 'app-step03b-dettaglio-bene',
  templateUrl: './step03b-dettaglio-bene.component.html',
  styleUrls: ['./step03b-dettaglio-bene.component.scss']
})
export class Step03bDettaglioBeneComponent implements OnChanges {

  constructor(private dataManager: DataManagerService) {
  }

  selectedID = null;
  @Input() dossierBene: Entity;
  @Input() idModello: number;

  setIDMisura(id) {
    this.selectedID = id;
  }

  confirmChanges() {
    this.dataManager.getContext().then(context => {
      this.dossierBene['IDVariante'] = this.selectedID;
      this.dataManager.saveContext(context);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!changes['idModello'].firstChange) {
      this.dossierBene['IDVariante'] = null;
    }
    this.selectedID = this.dossierBene['IDVariante'];
    this.confirmChanges();
  }
}



