import {ChangeDetectorRef, Component, EventEmitter, Output, ViewChild} from '@angular/core';
import {CamComponent} from '../cam.component';
import {DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'app-photo',
  templateUrl: './photo.component.html',
  styleUrls: ['./photo.component.scss']
})

export class PhotoComponent extends CamComponent {
  @Output() photo_done = new EventEmitter<Blob>();
  @ViewChild('streamView') streamView;

  constructor(ref: ChangeDetectorRef, private sanitizer: DomSanitizer) {
    super(ref);
  }

  /*
    Adjustable settings
 */
  /*
     END of Adjustable settings
  */
  photo: Blob = null;
  image_data = null;
  preview_hidden = true;

  takePhoto() {
    if (!this.stream) {
      return;
    }
    // Choose picture size (can be video width/height
    const width = this.streamView.nativeElement.videoWidth,
      height = this.streamView.nativeElement.videoHeight;

    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');

    canvas.width = width;
    canvas.height = height;
    context.drawImage(this.streamView.nativeElement, 0, 0, width, height);

    canvas.toBlob(result => {
      this.photo = result;
      const win: any = window;
      this.image_data = (win.URL || win.webkitURL).createObjectURL(this.photo);
      this.image_data = this.sanitizer.bypassSecurityTrustStyle('url(' + this.image_data + ')');
    });
    this.preview_hidden = false;
    this.stopStreams();
  }

  startCamera() {
    this.preview_hidden = true;
    this.photo = null;
    this.image_data = null;
    super.startCamera();
  }

  acceptPhoto() {
    if (!this.photo) {
      return;
    }

    this.photo_done.emit(this.photo);
    this.stopCamera();
  }

  rejectPhoto() {
    if (!this.photo) {
      return;
    }

    this.startCamera();
  }

}




