import {ChangeDetectorRef, Component, Input, OnChanges, SimpleChanges} from '@angular/core';
import {DataManagerService} from '../../services/data-manager.service';
import {Entity} from 'breeze-client';
import {AutoperiziaUtilitiesService} from '../../services/autoperizia-utilities.service';

@Component({
  selector: 'app-step03a-categoria-bene',
  templateUrl: './step03a-categoria-bene.component.html',
  styleUrls: ['./step03a-categoria-bene.component.scss']
})
export class Step03aCategoriaBeneComponent implements OnChanges {

  locali = [];
  pages = [];

  selectedID = -1;
  selectedLocale = '';
  slickConfig = {
    'slidesToShow': 1, 'slidesToScroll': 1, 'dots': true
  };
  @Input() dossierBene: Entity;
  // Needed to detect changes of fields on which my fields depends (with ngOnChanges)
  @Input() partitaBene: number;

  constructor(private dataManager: DataManagerService, private  ref: ChangeDetectorRef,
              private autoperiziaUtilities: AutoperiziaUtilitiesService) {
  }

  setLocale(locale) {
    this.selectedLocale = locale.Descrizione;
    this.selectedID = locale.ID;
  }

  confirmChanges() {
    this.dataManager.getContext().then(context => {
      this.dossierBene['IDModello'] = this.selectedID;
      this.dossierBene['Descrizione'] = this.selectedLocale;
      this.dataManager.saveContext(context);
    });
  }

  refresh() {
    this.locali = this.dataManager.getNestedProperty(this.dossierBene, ['DossierGaranzia_Ext', 'Partita', 'custom_Modelli'], []);
    const res = this.autoperiziaUtilities.chunkifyV2(this.locali);
    this.pages = res.pages;
    this.locali = res.elements;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!changes['partitaBene'].firstChange) {
      this.dossierBene['IDModello'] = null;
      this.dossierBene['Descrizione'] = null;
    }
    this.selectedID = this.dossierBene['IDModello'];
    this.selectedLocale = this.dossierBene['Descrizione'];
    this.refresh();
    this.confirmChanges();
  }
}



