import {AfterViewInit, ChangeDetectorRef, Component, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {DataManagerService} from '../services/data-manager.service';
import {NotificationComponent} from '../notification/notification.component';
import {NotificationType} from '../notification/notification-type.enum';

@Component({
  selector: 'app-autoperizia',
  templateUrl: './autoperizia.component.html',
  styleUrls: ['./autoperizia.component.scss']
})

export class AutoperiziaComponent implements AfterViewInit {

  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  dossierBeni = [];
  descrizioni = [];
  loadNotificationShown = false;

  @ViewChild('notification') notification: NotificationComponent;

  constructor(private _formBuilder: FormBuilder, private dataManager: DataManagerService, private  changeRef: ChangeDetectorRef) {
  }

  getDescriptionBene(bene, suffix = '', prefix = '') {
    const partita = (bene['DossierGaranzia_Ext'] || {})['Partita'];
    return partita ? prefix + partita['Descrizione'] + ' - ' + (partita['DossierGaranzie_Ext'][0]['Beni'].indexOf(bene) + 1) + suffix : '';
  }

  updateBeni() {
    this.dataManager.getContext().then(context => {
      this.dossierBeni = this.dataManager.getDossierBeni(context);

      this.dossierBeni.forEach(bene => {
        this.descrizioni[bene['ID']] = this.getDescriptionBene(bene, '', '');
      });
    });
  }

  addBene() {
    this.dataManager.getContext().then(context => {
      this.dataManager.addDossierBene(context);
      this.updateBeni();
    });
  }

  saveChanges() {
    this.dataManager.getContext().then(context => context.saveChanges());
  }

  ngAfterViewInit(): void {
    this.notification.show('Caricamento in corso', 'Attendere, caricamento del form in corso', NotificationType.Information);
  }

  modalShown() {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
    this.updateBeni();

    this.dataManager.getContext().then(() => {
      setTimeout(() => this.notification.hide(), 1000);
    });

    this.loadNotificationShown = true;
  }


}



