import {ChangeDetectorRef, Component, OnInit, ViewChild} from '@angular/core';
import {DataManagerService} from '../../services/data-manager.service';
import {DataService} from '../../services/dataService/data.service';
import {AutoperiziaUtilitiesService} from '../../services/autoperizia-utilities.service';

@Component({
  selector: 'app-step02-causa-sinistro',
  templateUrl: './step02-causa-sinistro.component.html',
  styleUrls: ['./step02-causa-sinistro.component.scss']
})
export class Step02CausaSinistroComponent implements OnInit {
  @ViewChild('causeList') causeList;

  hideGallery = false;
  cause = [];
  pages = [];
  selectedID = null;

  slickConfig = {
    'slidesToShow': 1, 'slidesToScroll': 1, 'dots': true
  };

  constructor(private dataManager: DataManagerService, private ref: ChangeDetectorRef,
              private autoperiziaUtilities: AutoperiziaUtilitiesService) {
  }

  setIDCausa(id) {
    this.selectedID = id;
  }

  fotoImpossibile() {
    this.hideGallery = true;
  }

  confirmChanges() {
    this.dataManager.getContext().then(context => {
      const entity = this.dataManager.getDossierPerizia(context);
      entity['IDCausaSinistro'] = this.selectedID;
      this.dataManager.saveContext(context);
    });
  }

  ngOnInit() {
    this.dataManager.getContext().then(context => {
      const causeArr = context.getEntities('CausaSinistro');
      const res = this.autoperiziaUtilities.chunkifyV2(causeArr);

      this.cause = res.elements;
      this.pages = res.pages;

      const dossierPerizia = this.dataManager.getDossierPerizia(context);
      // Entity already there, just load it and convert the values
      this.selectedID = dossierPerizia['IDCausaSinistro'];
    });
  }
}



