import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {CookieService} from 'angular2-cookie/core';
import {NgForage} from 'ngforage';
import {DataManagerService} from '../services/data-manager.service';

@Component({
  selector: 'app-chooser',
  templateUrl: './chooser.component.html',
  styleUrls: ['./chooser.component.scss']
})
export class ChooserComponent implements OnInit {

  constructor(private router: Router, private cookieService: CookieService, private ngf: NgForage, private dataManager: DataManagerService) {
  }

  ngOnInit() {
    if (this.cookieService.get('token') === undefined || this.cookieService.get('PIN') === undefined) {
      this.cookieService.removeAll();
      this.router.navigateByUrl('/login');
    } else {
      this.dataManager.getContext().catch(error => {
        console.log(error);
        this.cookieService.removeAll();
        this.router.navigateByUrl('/login');
      });
    }

  }

  openAutoperiziaComponent() {
    this.router.navigateByUrl('/autoperizia');
  }

  openVideoperiziaComponent() {
    this.router.navigateByUrl('/video-chat');
  }

}



