import {Component, OnInit, ViewChild} from '@angular/core';
import {Validators, FormControl} from '@angular/forms';
import {DataManagerService} from '../../services/data-manager.service';

@Component({
  selector: 'app-step06-informazioni-finali',
  templateUrl: './step06-informazioni-finali.component.html',
  styleUrls: ['./step06-informazioni-finali.component.scss']
})

export class Step06InformazioniFinaliComponent implements OnInit {

  optSelected: number;
  /*
  iban_regex = '/^(?:(?:IT|SM)\\d{2}[A-Z]\\d{22}|CY\\d{2}[A-Z]\\d{23}|NL\\d{2}[A-Z]{4}\\d{10}|LV\\d{2}[A-Z]{4}' +
    '\\d{13}|(?:BG|BH|GB|IE)\\d{2}[A-Z]{4}\\d{14}|GI\\d{2}[A-Z]{4}\\d{15}|RO\\d{2}[A-Z]{4}\\d{16}|KW\\d{2}[A-Z]{4}\\d{22}|MT\\d{2}[A-Z]{' +
    '4}\\d{23}|NO\\d{13}|(?:DK|FI|GL|FO)\\d{16}|MK\\d{17}|(?:AT|EE|KZ|LU|XK)\\d{18}|(?:BA|HR|LI|CH|CR)\\d{19}|(?:GE|DE|L' +
    'T|ME|RS)\\d{20}|IL\\d{21}|(?:AD|CZ|ES|MD|SA)\\d{22}|PT\\d{23}|(?:BE|IS)\\d{24}|(?:FR|MR|MC)\\d{25}|(?:AL|DO|L' +
    'B|PL)\\d{26}|(?:AZ|HU)\\d{27}|(?:GR|MU)\\d{28})$/i';
    */
  @ViewChild('iban') iban;
  @ViewChild('beneficiario') beneficiario;
  regex = '^([A-Za-z]{2}[ \\-]?[0-9]{2})(?=(?:[ \\-]?[A-Za-z0-9]){9,30}$)((?:[ \\-]?[A-Za-z0-9]{3,5}){2,6})([ \\-]?[A-Za-z0-9]{1,3})?$';
  iban_validator = new FormControl('', Validators.compose([
    Validators.pattern(this.regex)
  ]));

  constructor(private dataManager: DataManagerService) {
  }

  ngOnInit() {
  }
}



