import {Component, OnInit, ViewChild} from '@angular/core';
import {DataManagerService} from '../../services/data-manager.service';

import * as moment from 'moment';

@Component({
  selector: 'app-step01-data-sinistro',
  templateUrl: './step01-data-sinistro.component.html',
  styleUrls: ['./step01-data-sinistro.component.scss']
})
export class Step01DataSinistroComponent implements OnInit {
  @ViewChild('calendarModal') calendarModal;
  @ViewChild('calendar') calendar;


  constructor(private dataManager: DataManagerService) {
    moment.locale('it');
  }

  userAccuracy = '-1';
  dataSinistro = null;
  displayData = null;

  accuracyToEntity(value) {
    if (value === '0') {
      return true;
    } else if (value === '1') {
      return false;
    } else if (value === '2') {
      return null;
    }

    return null;
  }

  entityToAccuracy(value) {
    if (value === true) {
      return '0';
    } else if (value === false) {
      return '1';
    } else if (value === null) {
      return '2';
    }

    return '-1';
  }

  ngOnInit() {
    this.dataManager.getContext().then(context => {

      const dossierPerizia_ext = this.dataManager.getDossierPerizia(context);
      // Entity already there, just load it and convert the values
      const savedAccuracy = dossierPerizia_ext['DataPeriziaCerta'];
      this.userAccuracy = this.entityToAccuracy(savedAccuracy);
      this.dataSinistro = dossierPerizia_ext['DataPerizia'];
    });
  }

  changeAccuracy(newAcc) {
    this.userAccuracy = newAcc.value;

    if (this.userAccuracy !== '2' && this.dataSinistro !== null) {
      this.calendarModal.show();
    }

  }

  dateSelected(date) {
    this.displayData = this.dataSinistro ? moment(this.dataSinistro).format('DD-MMM-YYYY') : 'Nessuna data selezionata';
    this.calendarModal.hide();
  }

  confirmChanges() {
    this.dataManager.getContext().then(context => {
      const entity = this.dataManager.getDossierPerizia(context);
      entity['DataPerizia'] = this.dataSinistro;
      entity['DataPeriziaCerta'] = this.accuracyToEntity(this.userAccuracy);
      this.dataManager.saveContext(context);
    });
  }
}



