import {Injectable} from '@angular/core';

@Injectable()
export class AutoperiziaUtilitiesService {

  constructor() {
  }


  chunkify(array, elemsForPage = 6, balanced = true) {

    elemsForPage = Math.ceil(array.length / elemsForPage);
    if (elemsForPage < 2) {
      return [array];
    }

    const len = array.length,
      out = [];
    let size, i = 0;

    if (len % elemsForPage === 0) {
      size = Math.floor(len / elemsForPage);
      while (i < len) {
        out.push(array.slice(i, i += size));
      }
    } else if (balanced) {
      while (i < len) {
        size = Math.ceil((len - i) / elemsForPage--);
        out.push(array.slice(i, i += size));
      }
    } else {
      elemsForPage--;
      size = Math.floor(len / elemsForPage);
      if (len % size === 0) {
        size--;
      }
      while (i < size * elemsForPage) {
        out.push(array.slice(i, i += size));
      }
      out.push(array.slice(size * elemsForPage));
    }

    return out;
  }

  chunkifyV2(array, elemsForPage = 6) {
    const pagesNum = Math.ceil(array.length / elemsForPage);
    const pages = [];
    const elements = [];
    for (let i = 0; i < pagesNum; i++) {
      pages.push({num: i});
      elements.push([]);
    }

    array.forEach((element, i) => {
      elements[Math.floor(i / elemsForPage)].push(element);
    });

    return {pages: pages, elements: elements};
  }


}



