import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-step04-altri-danni',
  templateUrl: './step04-altri-danni.component.html',
  styleUrls: ['./step04-altri-danni.component.scss']
})
export class Step04AltriDanniComponent implements OnInit {
  @Output() addDanno = new EventEmitter<any>();

  selectedValue = '';

  constructor() {
  }

  ngOnInit() {
  }

  confirmChanges() {
    if (this.selectedValue === '0') {
      this.addDanno.emit();
    }
  }
}



