import {ChangeDetectorRef, Component, OnInit, ViewChild} from '@angular/core';
import {DataManagerService} from '../services/data-manager.service';
import {AutoperiziaUtilitiesService} from '../services/autoperizia-utilities.service';

@Component({
  selector: 'app-appointment',
  templateUrl: './appointment.component.html',
  styleUrls: ['./appointment.component.scss']
})
export class AppointmentComponent implements OnInit {

  @ViewChild('calendarModal') calendarModal;
  @ViewChild('timeList') timeList;

  userAccuracy = '2';
  dataSinistro = 'Nessuna data selezionata';
  dataSelected = false;
  hideData = false;
  hideCalendar = true;
  mytime = [];
  pages = [];
  timeHidden = [];
  selectedID = null;
  hideButtons = true;

  slickConfig = {
    'slidesToShow': 1, 'slidesToScroll': 1, 'dots': true
  };

  constructor(private dataManager: DataManagerService, private ref: ChangeDetectorRef,
              private autoperiziaUtilities: AutoperiziaUtilitiesService) {
  }

  ngOnInit() {
    this.dataSelected = false;
  }

  dateSelected(date) {
    this.hideCalendar = true;
    this.hideData = false;
    this.dataSelected = true;
    this.dataSinistro = date._value.toLocaleDateString();
    this.calendarModal.hide();
    this.hideButtons = false;

    const times = [
      {id: 0, desc: '9:00 - 9:30'},
      {id: 1, desc: '9:30 - 10:00'},
      {id: 2, desc: '10:00 - 10:30'},
      {id: 3, desc: '10:30 - 11:00'},
      {id: 4, desc: '11:00 - 11:30'},
      {id: 5, desc: '11:30 - 12:00'},
      {id: 6, desc: '12:00 - 12:30'},
      {id: 7, desc: '12:30 - 13:00'},
      {id: 8, desc: '13:00 - 13:30'},
      {id: 9, desc: '13:30 - 14:00'},
      {id: 10, desc: '14:00 - 14:30'},
      {id: 11, desc: '15:30 - 16:00'},
      {id: 12, desc: '16:00 - 16:30'},
      {id: 13, desc: '16:30 - 17:00'},
      {id: 14, desc: '17:00 - 17:30'},
      {id: 15, desc: '17:30 - 18:00'}
    ];

    const res = this.autoperiziaUtilities.chunkifyV2(times, 4);
    this.pages = res.pages;
    this.mytime = res.elements;
  }

  editDate() {
    this.calendarModal.show();
  }

  confirmChanges() {
    console.log('call the corresponding service');
  }

  setTime(id) {
    this.selectedID = id;
  }

}



